body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgba(8,45,100,0.5) 0%, rgba(4,200,43,0.5) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


img:hover {
  opacity: 0;
}


html,body {
	size: 100%;
}

@font-face {
	font-family: 'Bauhaus 93';
	font-style: normal;
	font-weight: bolder;
	src: local('Bauhaus 93'), url(/robofriends-react-tutorial/static/media/BAUHS93.ba85c44c.TTF) format('TTF');
}

h1 {
	font-family: 'Bauhaus 93';
	-webkit-transform: scale(1);
	        transform: scale(1);
}



